export const verifyPhone = (value = '')=>{
	const phoneRule = /^1[3456789]\d{9}$/
	return Boolean(phoneRule.test(value))
}

export const computedRequired = (data) =>{
	let age = `${data.ageMin} ~ ${data.ageMax} `
	let degree = ''
	let exprience = ''
	switch (data.educationDegree){
		case 0:
			degree = ''
			break;
		case 1:
			degree = ''
			break;
		case 2:
			degree = ''
			break;
		case 3:
			degree = ''
			break;
		default:
			degree = ''
			break;
	}
	switch (data.workExperience){
		case 0:
			exprience = ''
			break;
		case 1:
			exprience = ''
			break;
		case 2:
			exprience = ''
			break;
		case 3:
			exprience = ''
			break;
		default:
			exprience = ''
			break;
	}
	return `${age} | ${degree} | ${exprience}`
}

export const formatDate = (date, fmt) => {
  var currentDate = new Date(date);
  var o = {
    "M+": currentDate.getMonth() + 1, //
    "d+": currentDate.getDate(), //
    "h+": currentDate.getHours(), //
    "m+": currentDate.getMinutes(), //
    "s+": currentDate.getSeconds(), //
    "q+": Math.floor((currentDate.getMonth() + 3) / 3), //
    "S": currentDate.getMilliseconds() //
  };
  if (/(y+)/.test(fmt)) fmt = fmt.replace(RegExp.$1, (currentDate.getFullYear() + "").substr(4 - RegExp.$1.length));
  for (var k in o)
    if (new RegExp("(" + k + ")").test(fmt)) fmt = fmt.replace(RegExp.$1, (RegExp.$1.length == 1) ? (o[k]) : (("00" + o[k]).substr(("" + o[k]).length)));
  return fmt;
}
