import { HTTP } from '../utils/http.js'

class ProcessModel extends HTTP {
	constructor(arg) {
		super()
	}
	// 
	processList(receviedId){
		return this.request({
			url: '/process/processList',
			data: {
				receviedId
			}
		})
	}
	// 
	cancelResume(id){
		return this.request({
			url : '/process/cancelResume',
			data: {
				id
			}
		})
	}
	
	//  (B)
	interviewFeedback(processInfoId, feedbackStatus, time){
		return this.request({
			url : '/process/interviewFeedback',
			data: {
				processInfoId, 
				feedbackStatus, 
				time
			}
		})
	}
	
	// 
	entryFeedBack(processInfoId, feedbackStatus, entryTime){
		return this.request({
			url : '/process/entryFeedback',
			data: {
				processInfoId, 
				feedbackStatus, 
				entryTime
			}
		})
	}
	// 
	quitFeedBack(processInfoId, quitTime){
		return this.request({
			url : '/process/quitFeedback',
			data: {
				processInfoId, 
				quitTime
			}
		})
	}
	// 
	isKnow(processInfoId){
		return this.request({
			url : '/process/isKnow',
			data: {
				processInfoId
			}
		})
	}
	// 
	appeal(processInfoId, content){
		return this.request({
			url : '/process/appeal',
			data: {
				processInfoId,
				content
			}
		})
	}
	// 
	approval(processInfoId){
		return this.request({
			url : '/process/approval',
			data: {
				processInfoId
			}
		})
	}
	// B
	appealFeedback(processInfoId, content){
		return this.request({
			url : '/process/appealFeedback',
			data: {
				processInfoId,
				content
			}
		})
	}
	// B
	approvalAppeal(processInfoId){
		return this.request({
			url : '/process/approvalAppeal',
			data: {
				processInfoId
			}
		})
	}
} 

export default ProcessModel